    type: 'info',
    title: self.car.selected.name,
    html: `${'<div class="calc__modal">'
            + '<div class="calc__modal-desc">'}${self.car.selected.desc}</div>`
            + '<div class="calc__modal-charater">'
            + '<div class="calc__modal-text"> </div>'
